
'use client';

import * as React from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import { Badge } from "@/components/ui/badge";
import { Trash2 } from "lucide-react";
import { format } from "date-fns";
import type { SavedSpeech, User } from "@/lib/types";
import { useFirebase, useCollection, useMemoFirebase } from "@/firebase";
import { collection, query, where } from "firebase/firestore";
import { useAuth } from "@/contexts/auth-context";

type PerformanceEventType = 'humorous' | 'dramatic' | 'duo' | 'duet' | 'prose' | 'poetry';

export const SavedRecordings = ({ 
    eventType, 
    onView,
    onDelete,
}: { 
    eventType: PerformanceEventType, 
    onView: (speech: SavedSpeech) => void,
    onDelete: (speech: SavedSpeech) => void,
}) => {
    const { user } = useAuth();
    const { firestore } = useFirebase();

    const userSpeechesQuery = useMemoFirebase(() => {
        if (!firestore || !user) return null;
        return query(collection(firestore, 'speeches'), where('ownerId', '==', user.id), where('mode', '==', eventType));
    }, [firestore, user, eventType]);
    const { data: userSpeeches } = useCollection<SavedSpeech>(userSpeechesQuery);

    const sharedSpeechesQuery = useMemoFirebase(() => {
        if (!firestore || !user) return null;
        return query(collection(firestore, 'speeches'), where('sharedWith', 'array-contains', user.id), where('mode', '==', eventType));
    }, [firestore, user, eventType]);
    const { data: sharedSpeeches } = useCollection<SavedSpeech>(sharedSpeechesQuery);
    
    const relevantRecordings = React.useMemo(() => {
        const all = new Map<string, SavedSpeech>();
        (userSpeeches || []).forEach(s => all.set(s.id, s));
        (sharedSpeeches || []).forEach(s => all.set(s.id, s));
        return Array.from(all.values()).sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
    }, [userSpeeches, sharedSpeeches]);

    return (
         <Card>
            <CardHeader>
                <CardTitle>Saved Recordings</CardTitle>
                <CardDescription>Review your past performances for this event.</CardDescription>
            </CardHeader>
            <CardContent>
                 {relevantRecordings.length > 0 ? (
                    <ul className="space-y-2">
                        {relevantRecordings.map(rec => (
                            <li 
                                key={rec.id} 
                                className="flex items-center justify-between p-3 rounded-md border bg-muted/50"
                            >
                                <div className="flex-1 min-w-0">
                                    <div className="flex items-center gap-2">
                                        <p className="text-sm font-medium truncate">{rec.topic}</p>
                                        <Badge variant="outline" className="capitalize text-xs">{rec.mode}</Badge>
                                    </div>
                                    <p className="text-xs text-muted-foreground mt-1">Recorded on {format(new Date(rec.date), 'PPP')}</p>
                                </div>
                                <div className="flex items-center gap-1">
                                    <Button size="sm" variant="secondary" onClick={() => onView(rec)}>View</Button>
                                    {rec.ownerId === user?.id && (
                                        <AlertDialog>
                                            <AlertDialogTrigger asChild>
                                                <Button size="icon" variant="destructive" className="h-9 w-9"><Trash2 className="h-4 w-4" /></Button>
                                            </AlertDialogTrigger>
                                            <AlertDialogContent>
                                                <AlertDialogHeader>
                                                    <AlertDialogTitle>Delete Recording?</AlertDialogTitle>
                                                    <AlertDialogDescription>This will permanently delete this recording. This action cannot be undone.</AlertDialogDescription>
                                                </AlertDialogHeader>
                                                <AlertDialogFooter>
                                                    <AlertDialogCancel>Cancel</AlertDialogCancel>
                                                    <AlertDialogAction onClick={() => onDelete(rec)}>Delete</AlertDialogAction>
                                                </AlertDialogFooter>
                                            </AlertDialogContent>
                                        </AlertDialog>
                                    )}
                                </div>
                            </li>
                        ))}
                    </ul>
                ) : (
                    <div className="text-center text-sm text-muted-foreground py-8 border border-dashed rounded-md">
                        No recordings for this event yet.
                    </div>
                )}
            </CardContent>
        </Card>
    )
}
